* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

.menuBtn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.menuBtn div,
.menuBtn div:before,
.menuBtn div:after {
  background: #1a1a1a;
  border-radius: 10px;
  width: 40px;
  height: 5px;
  transition: all 0.15s linear;
}

.menuBtn div:before,
.menuBtn div:after {
  content: "";
  position: absolute;
}

.menuBtn div:before {
  transform: translateY(-200%);
}

.menuBtn div:after {
  transform: translateY(200%);
}

.menuBtn.open div {
  background: transparent;
}

.menuBtn.open div:before {
  transform: rotate(45deg);
}

.menuBtn.open div:after {
  transform: rotate(-45deg);
}

.title {
  text-align: center;
  font-size: 45px;
  margin: 25px 0;
  color: #000;
}

.item {
  background: rgba(241, 230, 230, 0.938);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 0px 20px 2px #ccc;
  max-width: 310px;
}

.item .item_header a {
  display: block;
  max-width: 310px;
  max-height: 210px;
  overflow: hidden;
}

.item .item_header a img {
  display: block;
  width: 100%;
  min-height: 210px;
  object-fit: cover;
  transition: all 0.02s;
}

.item .item_footer {
  padding: 7px;
}

.item .item_footer h3 {
  margin-top: 10px;
}

.item .item_footer h3 a {
  background: none;
  color: #000;
}

.item .item_footer .see_more {
  display: block;
  text-align: center;
  border-radius: 16px;
  margin-top: 20px;
  background: rgb(120, 236, 240);
  color: #000;
  padding: 7px 14px;
}

.item:hover .item_header a img {
  transform: scale(1.2);
}

.top_nav {
  background: cyan;
}

.top_nav .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 30px;
  width: 100%;
}

.top_nav .top_nav_block {
  display: flex;
}

.top_nav .top_nav_block a {
  display: block;
  text-decoration: none;
  margin-right: 15px;
}

.top_nav .top_nav_block a svg {
  width: 40px;
  height: 40px;
  fill: white;
}

.top_nav .top_nav_block a:last-child {
  margin-right: 0px;
}

.top_nav .top_nav_block a {
  font-weight: bold;
}

.top_nav .top_nav_block .search-box {
  display: flex;
  position: relative;
  background: white;
  height: 35px;
  border-radius: 40px;
  margin-right: 10px;
  width: 240px;
}

.top_nav .top_nav_block .search-box input {
  background: none;
  outline: none;
  border: none;
  width: 190px;
  margin-left: 15px;
  color: rgb(19, 18, 18);
  font-size: 16px;
  transition: 0.4s;
  line-height: 15px;
}

.top_nav .top_nav_block .search-btn {
  display: flex;
  color: cyan;
  float: right;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background: white;
  justify-content: center;
  align-items: center;
}

.top_nav .top_nav_block #sign_in {
  width: 40px;
  height: 40px;
}

nav {
  position: sticky;
  background: #fff;
  position: sticky;
  top: 0;
  left: 0;
  z-index: 99999;
  box-shadow: 0 6px 36px rgba(0, 0, 0, 0.09);
}

nav .container {
  display: flex;
  justify-content: space-between;
  min-height: 80px;
  align-items: center;
  position: relative;
}

nav .container .nav_logo {
  display: flex;
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
}

nav .container .nav_logo img {
  width: 50px;
  height: 50px;
}

nav .container .nav_menu .menuBtn {
  display: none;
}

nav .container .nav_menu ul {
  display: flex;
}

nav .container .nav_menu ul li {
  list-style-type: none;
}

nav .container .nav_menu ul li a {
  display: flex;
  height: 80px;
  align-items: center;
  padding: 8px;
  color: #1a1a1a;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;
  z-index: 5;
  transition: color 0.4s;
  letter-spacing: 1px;
}

nav .container .nav_menu ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0%;
  top: 0;
  left: 0;
  background: cyan;
  transition: height 0.3s;
  z-index: -1;
}

nav .container .nav_menu ul li a:hover {
  color: red;
}

nav .container .nav_menu ul li a:hover::after {
  height: 100%;
}

