header {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center center;
  background-attachment: fixed;
  position: relative;
  height: 90vh;
  overflow: hidden;
}

header::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(98, 242, 247, 0.1215686275);
}

header .header_content {
  display: flex;
  flex-flow: column;
  position: relative;
  color: rgb(0, 0, 0);
  z-index: 55;
  text-align: left;
  margin-left: 30px;
  margin-top: 0;
  width: 100%;
}

header .header_content h1 {
  text-transform: uppercase;
  font-size: 55px;
  font-weight: bold;
  letter-spacing: 2px;
}

header .header_content p {
  line-height: 30px;
  margin-bottom: 20px;
  max-width: 600px;
}

header .header_content .header_content__buttons {
  display: flex;
}

header .header_content .header_content__buttons a,
header .header_content .header_content__buttons button {
  display: block;
  color: #000;
  background: white;
  padding: 15px 40px;
  border-radius: 16px;
  border: none;
  cursor: pointer;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
  background: transparent;
  font-size: 16px;
}

header .header_content .header_content__buttons a::before,
header .header_content .header_content__buttons button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background: cyan;
  transition: width 0.2s;
  z-index: -1;
}

header .header_content .header_content__buttons a:hover::before,
header .header_content .header_content__buttons button:hover::before {
  width: 100%;
  z-index: -1;
}

main {
  height: 3500px;
}

main .items_wrapper {
